// src/components/Footer.js

import React from 'react';
import { Box, Typography, Grid, Link, TextField, Button } from '@mui/material';

const Footer = () => {
  return (
    <Box
      component="footer"
      sx={{
        backgroundColor: 'primary.main',
        color: 'secondary.main',
        padding: { xs: 4, md: 6 },
      }}
    >
      <Grid container spacing={4}>
        {/* Company Info */}
        <Grid item xs={12} sm={6} md={3}>
          <Typography variant="h6" gutterBottom>
            DTSYS
          </Typography>
          <Typography variant="body2" sx={{ lineHeight: 1.6 }}>
            03-6211 3678<br />
            <Link href="mailto:info@dtsys.tech" sx={{ color: 'secondary.main', textDecoration: 'none' }}>info@dtsys.tech</Link><br />
            No. B-5-13, Gateway Corporate Suites,<br />
            Mont Kiara, 50480 Kuala Lumpur,<br />
            Wilayah Persekutuan Malaysia
          </Typography>
        </Grid>

        {/* Quick Links */}
        <Grid item xs={12} sm={6} md={3}>
          <Typography variant="h6" gutterBottom>
            Quick Links
          </Typography>
          <Link href="#solutions" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'block', mb: 1 }}>
            Solutions
          </Link>
          <Link href="#services" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'block', mb: 1 }}>
            Services
          </Link>
          <Link href="#" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'block', mb: 1 }}>
            About Us
          </Link>
          <Link href="#" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'block', mb: 1 }}>
            Blog
          </Link>
          <Link href="#" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'block', mb: 1 }}>
            Submit
          </Link>
        </Grid>

        {/* Newsletter Subscription */}
        <Grid item xs={12} sm={6} md={3}>
          <Typography variant="h6" gutterBottom>
            Subscribe to Our Newsletter
          </Typography>
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              gap: 2,
              mt: 1,
            }}
          >
            <TextField
              variant="outlined"
              size="small"
              placeholder="Email *"
              sx={{
                backgroundColor: 'background.paper',
                borderRadius: 1,
              }}
            />
            <Button
              variant="contained"
              sx={{
                backgroundColor: 'secondary.main',
                color: 'primary.main',
                '&:hover': {
                  backgroundColor: 'grey.300',
                },
                fontWeight: 'bold',
                textTransform: 'none',
                padding: '8px 16px',
              }}
            >
              Get Started
            </Button>
          </Box>
        </Grid>

        {/* Social Media Links */}
        <Grid item xs={12} sm={6} md={3}>
          <Typography variant="h6" gutterBottom>
            Follow Us On:
          </Typography>
          <Box
            sx={{
              display: 'flex',
              gap: 2,
              mt: 1,
            }}
          >
            <Link href="#" aria-label="LinkedIn">
              <img src="../images/linkedin.png" alt="LinkedIn" height="30" />
            </Link>
            <Link href="#" aria-label="Facebook">
              <img src="../images/facebook.png" alt="Facebook" height="30" />
            </Link>
            <Link href="#" aria-label="Twitter">
              <img src="../images/twitter.png" alt="Twitter" height="30" />
            </Link>
          </Box>
        </Grid>
      </Grid>

      {/* Footer Bottom */}
      <Box textAlign="center" mt={4}>
        <Typography variant="body2">
          2024 by DTSYS. All rights reserved.
        </Typography>
      </Box>
    </Box>
  );
};

export default Footer;
