// src/components/Header.js

import React from 'react';
import { AppBar, Toolbar, Button, Box } from '@mui/material';
import logo from '../images/Logo.png';

const Header = () => {
  // Scroll helper function
  const scrollToSection = (id) => {
    const element = document.getElementById(id);
    if(element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <AppBar
      position="sticky"
      sx={{
        backgroundColor: 'primary.main', // Uses theme.palette.primary.main
      }}
    >
      <Toolbar>
        {/* Logo Section */}
        <Box display="flex" alignItems="center">
          <img
            src={logo}
            alt="DTSYS Logo"
            style={{
              height: '40px',
            }}
          />
        </Box>

        {/* Navigation Links */}
        <Box
          sx={{
            marginLeft: 'auto', // Pushes the nav links to the right
            display: 'flex',
            gap: 3, // Equivalent to theme.spacing(3)
          }}
        >
          <Button
            onClick={() => scrollToSection('hero')}
            sx={{
              color: 'secondary.main', // Uses theme.palette.secondary.main (usually white)
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Home
          </Button>
          <Button
            onClick={() => scrollToSection('services')}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Services
          </Button>
          <Button
            onClick={() => scrollToSection('solutions')}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Solutions
          </Button>
          <Button
            onClick={() => scrollToSection('partners')}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Partners
          </Button>
          <Button
            onClick={() => scrollToSection('contact')}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Contact Us
          </Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
